.all{
  display: flex;
  justify-content: center;
}
.chessboard  {
  display: flex;
  flex-direction: column;
  width: 560px;
  border: 2px solid #333;
}
.row {
  display: flex;
}
h1{
  text-align: center;
}
.square {
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
}

.light {
  background-color: #f0d9b5;
}

.dark{
  background-color: #b58863;
}

.piece {
  color: black; 
}
